import { initializeApp } from "firebase/app";
import { getMessaging, getToken, onMessage } from "firebase/messaging";
const firebaseConfig = {
  apiKey: process.env.REACT_APP_FIREBASE_API_KEY,
  authDomain: process.env.REACT_APP_FIREBASE_AUTH_DOMAIN,
  projectId: process.env.REACT_APP_FIREBASE_PROJECT_ID,
  storageBucket: process.env.REACT_APP_FIREBASE_STORAGE_BUCKET,
  messagingSenderId: process.env.REACT_APP_FIREBASE_MESSAGING_SENDER_ID,
  appId: process.env.REACT_APP_FIREBASE_APP_ID,
};
const app = initializeApp(firebaseConfig);
const messaging = getMessaging(app);
export async function requestFCMToken(){
  try{ return await getToken(messaging,{vapidKey:process.env.REACT_APP_FIREBASE_VAPID_KEY}); }
  catch(e){ console.error(e); return null; }
}
export function listenForeground(setter){ onMessage(messaging,(p)=>{ const {title, body}=p.notification||{}; setter(title?`${title}: ${body}`:JSON.stringify(p)); }); }
